.heading {
    text-align: center;
    font-size: 3vw;
    font-weight: 100;
    color: #1B1B2F;
}

.layout {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.searchSection {
    width: 100%;
    margin-left: 10px;
    margin-bottom: 15px;
    padding: 8px 0;
    display: flex;

}

.input {
    font-size: 3.2vw;
    border-radius: 5px;
    font-weight: 400;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.75);
    margin-left: 15px;

}

.input__actions {
    color: black;
    border-radius: 40%;
    margin-left: 12px;
}

label {
    color: black;
    font-size: 2.4vw;
    margin-left: 8px;
}


@media (max-width: 680px) {
    .heading {
        font-size: 5.5vw;
    }

    label {
        color: black;
        font-size: 4.4vw;
        margin-left: 8px;
    }

    .input {
        font-size: 3.8vw;
        border-radius: 5px;
        font-weight: 400;
        box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.75);
        font-weight: 600;
        width: 36%;
    }
}