.card {
    padding: 7px 6px;
    margin: 16px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 250px;
    background-color: #064663;
    border-radius: 8px;
    font-family: 'Manrope', sans-serif;
    position: relative;
}

@media (max-width:650px) {
    .card {
        width: 70%;
    }

}

@media (min-width:650px) and (max-width:800px) {
    .card {
        width: 40%;
    }

}

@media (min-width:1000px) and (max-width:1300px) {
    .card {
        width: 30%;
    }

}

@media (min-width:1300px) and (max-width:1500px) {
    .card {
        width: 20%;
    }

}

.rating {
    background-color: #041C32;
    padding: 5px;
    border-radius: 35%;
    position: absolute;
    right: 6px;
    top: 2px;
}

.card:hover,
card:focus {
    background-color: #041C32;
    cursor: pointer;
}

.poster {
    border-radius: 6px;
    width: 100%;
}




.title {
    padding: 10px 0px 20px 0px;
    width: 100%;
    text-align: center;
    justify-content: center;
    font-size: 1.5rem;
    margin-bottom: 5px;
}

.details {
    display: flex;
    justify-content: space-between;
    padding: 0 12px;
}

.details span {
    text-transform: uppercase;
    font-weight: 100;

}