.layout {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: #041C32;
    box-shadow: 0px 1px 5px black;
    font-family: 'Manrope', sans-serif;
    justify-content: space-around;
    color: #fff;
    z-index: 100;
    padding-bottom: 12px;
}

.header {
    width: 100%;
    text-align: center;
    font-size: 5vw;

}


@media (max-width: 700px) {
    .header {
        padding-top: 10px;
        font-size: 6.4vw;
    }
}

@media (max-width: 700px) {
    .layout {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .header {
        text-align: center;
    }


}