.navbar {
    background-color: #041C32;
    color: #fff;
    display: flex;
    justify-content: space-around;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    position: fixed;
    z-index: 100;
    font-size: 4.2vw;
    font-family: 'Manrope', sans-serif;
}

@media (min-width: 700px) {
    .navbar {
        font-size: 2.6vw;
    }
}

li {
    list-style: none;
}

a {
    list-style: none;
    text-decoration: none;
    color: #fff;
}